@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

body {
  background: linear-gradient(to right, #182848, #4b6cb7);
  color: #000;
}

a {
  color: #fff;
  text-decoration: none;
}
a:hover {
  color: #182848;
}

.notes-page {
  width: 100%;
  min-height: 100vh;
  color: whitesmoke;
}

.notes-page header {
  max-width: 1920px;
  min-height: 100px;
  overflow: hidden;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px 0 0;
}

.notes-page header .logo {
  text-transform: uppercase;
  font-family: 'Audiowide', cursive;
  background: #f12711;
  background: linear-gradient(to right, #f5af19, #f12711);
  padding: 6px 20px;
  border-radius: 0 5px 40px 0;
  letter-spacing: 2px;
}

.notes-page header ul li {
  list-style: none;
  display: inline-block;
  padding: 0 15px;
}

.notes-page header ul li a {
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  padding: 10px 16px;
  border-radius: 5px;
  transition: background-color 1s;
}

.notes-page header ul li a:hover {
  background-color: white;
  color: #182848;
}

.mode-toggle {
  /* padding: 0 16px; */
  cursor: pointer;
  position: relative;
  top: 10px;
}

.notes-page section {
  width: 100%;
  box-shadow: 0 0 5px rgb(7, 25, 63);
  padding: 20px 0;
}

.note-wrapper {
  max-width: 1920px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: auto;
  border-radius: 10px;
}

.card {
  width: 300px;
  box-shadow: 0 0 5px rgb(7, 25, 63);
  padding: 10px;
  position: relative;
  color: #ddd;
  margin: 15px;
  cursor: pointer;
  border: 2px solid #ccc;
}

.card .close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-weight: 900;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  color: white;
}

.card h3 {
  margin-right: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
}

.card .text-wrapper {
  width: 100%;
  /* height: 150px; */
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 10;
  overflow: hidden;
  margin: 10px 0;
  color: #fff;
}

.card .text-wrapper p {
  text-align: justify;
  text-align-last: left;
}

.card .date {
  text-align: right;
  color: wheat;
  position: relative;
  bottom: 10px;
}

.card-footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 50px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 12px;
  position: absolute;
  bottom: 0px;
}

/* Create Note Styling */
.create-note {
  max-width: 500px;
  width: 100%;
  margin: auto;
  border: 10px solid #ccc;
  padding: 30px;
}

.create-note h2 {
  letter-spacing: 2px;
  text-transform: uppercase;
  margin: 10px 0;
}

.create-note .row {
  width: 100%;
  margin: 10px 0;
}

.create-note input,
.create-note button {
  width: 100%;
  height: 40px;
  padding: 0 5px;
}

.create-note label {
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 2px;
}

.create-note textarea {
  width: 100%;
  resize: none;
  padding: 0 5px;
}

.create-note button {
  width: 50%;
  margin-left: 25%;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 900;
  letter-spacing: 2px;
  color: rgb(7, 25, 63);
}

@media (max-width: 500px) {
  .create-note {
    padding: 15px;
    border: 5px solid #ccc;
  }
}

/* Login-Register Styling */
.login-page {
  position: relative;
  width: 100%;
  min-height: 100vh;
}

.login input,
.register input {
  margin-bottom: 15px;
}

.login,
.register {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: rgb(63, 9, 63);
}

.login-page h2,
.login-page h3 {
  color: #fff8;
  text-align: center;
  text-transform: uppercase;
}

.login-page h3 {
  margin-top: 15px;
}

.login-page p {
  margin: 10px 0;
}

.login-page span:hover {
  color: #f5af19;
  cursor: pointer;
}

